@charset 'utf-8';
@import 'base/global.scss';

/* 
 * loyout.scss 
 * 框架内元素, 比如按钮, 编辑框等
*/

// 按钮
.btn {
  @extend %ib;
  @extend %ellipsis;
  text-align: center;
  padding: 0px 2em;
  height: 2.3em;
  line-height: 2.3em;
  border-radius: 0.3em;
  color: #fff;
  border: px2rem(1px) solid $Theme-color;
  background-color: $Theme-color;
  font-size: 1em;
  // font-family: "PingFang SC", "Microsoft yahei", "Helvetica Neue", Helvetica, Arial, sans-serif;
  cursor: pointer;
  -webkit-appearance: none;

  &:hover {
    color: #fff;
    background-color: $Theme-color-light;
  }
  &.disabled {
    pointer-events: none;
    cursor: not-allowed;
    background-color: gainsboro;
    border: px2rem(1px) solid gray;
    color: gray;
  }
  &.loading {
    pointer-events: none;
    cursor: not-allowed;
    opacity: 0.7;
    filter: alpha(opacity=70);
  }

}

// 小型按钮尺寸
.btn-small {
  @extend .btn;
  font-size: 0.9em;
  padding: 0px 1em;
}

// 标题
.title-h1 {
  padding: 0 0.8em;
  margin-bottom: 0.8em;
  line-height: 3.5em;
  color: #333;
  background: #f9f9f9;
  border-bottom: 1px solid #e4e4e4;

  .primary {
    font-size: 1.5em;
    color: black;
  }

}

// 子标题
.title-h2 {
  padding: 0 0.8em;
  margin-bottom: 0.8em;
  line-height: 2.3em;
  color: #333;
  background: #f9f9f9;
  border-bottom: 1px solid #e4e4e4;

  .primary {
    font-size: 1.1em;
    color: black;
  }
}

.title-h1.noBottom,
.title-h2.noBottom {
  margin-bottom: 0;
  border-bottom: none;
}


// 加载等待图片
.loading-img {
  @extend %full-size;
  display: none;
  background: rgba(0, 0, 0, 0.1);
  z-index: 10;
  // filter: progid:DXImageTransform.Microsoft.gradient(startColorstr=#19000000,endColorstr=#19000000);

  .img {
    @extend %full-size;
    background: url($images-path + 'loading.gif') center center no-repeat;
  }
}

// 上传文件
.file-wrap {
  @extend %ib;
  position: relative;
  min-width: px2rem(100px);

  input[type="file"] {
    @extend %full-size;
    z-index: 2;
    opacity: 0.01;
    filter: progid:DXImageTransform.Microsoft.Alpha(opacity=1);
    cursor: pointer;
  }
}



/* 表单布局 */
.query-box {
  padding: 0.8em;
}

// 通用表单
.item {
  margin-bottom: 0.8em;
  position: relative;
  .input-text,
  .input-select {
    width: px2rem(170px);
  }
  .label {
    min-width: px2rem(95px);
    text-align: right;
    margin-right: 0.8em;
    vertical-align: middle;
  }
}
.input-area {
  position: relative;
  .cut {
    vertical-align: middle;
  }
}
.label,
.input-wrap {
  position: relative;
  @extend %ib;
}

// 自适应表单
.adaptive {
  text-align: left;
  .item {
    @extend %ib;
    float: left;
    .input-text,
    .input-select {
      width: px2rem(170px);
    }
  }
  .label {
    @extend %ib;
    // @extend %ellipsis;
    width: 20%;
    text-align: right;
    margin-right: 0.8em;
    vertical-align: middle;
  }
}

[data-dpr="2"] {
  .item .label.w-90 {
    width: px2rem(95px);
  }
}


/* 竖屏 */
@media only screen and (orientation: portrait) {
  /* 小于于等于宽度 */
  @media only screen and (max-width: 769px) {
    .adaptive .item {
      width: 100%;
    }
    .adaptive .item .label {
      width: 18%;
    }

  }
  /* 大于等于宽度 */
  @media only screen and (min-width: 770px) {
    .adaptive .item {
      width: 50%;
    }
    .adaptive .item .label {
      width: 10%;
    }
  }
  /* 大于等于宽度 */
  @media only screen and (min-width: 1140px) {
    .adaptive .item {
      width: 33.33%;
    }
    .adaptive .item .label {
      width: 10%;
    }

    // 处理 6 Plus
    [data-dpr="3"] {
      .adaptive .item {
        width: 100%;
      }
    }


  }
  /* 大于等于宽度 */
  @media only screen and (min-width: 1680px) {
    .adaptive .item {
      width: 25%; 
    }
    .adaptive .item .label {
      width: 10%;
    }
  }
}


/* 横屏 */
@media only screen and (orientation: landscape) {
  /* 小于于等于宽度 */
  @media only screen and (max-width: 950px) {
    .adaptive .item {
      width: 100%;
    }
    .adaptive .item .label {
      width: 18%;
    }
  }

  /* 大于等于宽度 */
  @media only screen and (min-width: 951px) {
    .adaptive .item {
      width: 50%;
    }
    .adaptive .item .label {
      width: 10%;
    }
  }

  /* 大于等于宽度 */
  @media only screen and (min-width: 1140px) {
    .adaptive .item {
      width: 33.33%;
    }
    .adaptive .item .label {
      width: 10%;
    }

    // 处理 6 Plus
    [data-dpr="3"] {
      .adaptive .item {
        width: 100%;
      }
    }


  }
  /* 大于等于宽度 */
  @media only screen and (min-width: 1680px) {
    .adaptive .item {
      width: 25%; 
    }
    .adaptive .item .label {
      width: 10%;
    }
  }
}










/* 查询结果框架 */
.query-result {
  position: relative;
  -webkit-overflow-scrolling: touch;
  overflow: auto;
  width: 100%;
  height: 500px;

  iframe {
    width: 100%;
    height: 100%;
  }
}

.table-warp {
  width: 100%;
  min-width: 100%;
  overflow-x: auto;
  -webkit-overflow-scrolling: touch;
}


