@charset 'utf-8';

/* ===主题颜色=== */
// 蓝色 #03a9f4     橙色 #FF9800
$Theme-color: #03a9f4;
$Theme-color-light: lighten($Theme-color, 10%);
$Theme-color-dark: darken($Theme-color, 10%);
$Theme-gray-dark: #d3d3d3;
$Theme-gray-middle: #eee;
$Theme-gray-light: #f5f5f5;
$Theme-text-primary: #333;
$Theme-text-secondary: #666;
$Theme-text-third: #999;
$Theme-link: rgb(3, 169, 244); /* 链接 */
$Theme-link-hover: rgb(44, 191, 255); /* 链接悬停 */


/* ===环境变量=== */
// 图片目录
$images-path: "../images/";
// 页面最小宽度
$min-width: 1020px;
// 字体
$fonts: "PingFang SC","Microsoft yahei","Helvetica Neue",Helvetica,Arial,sans-serif;
// dpr
$ratio: 54px;

/* ===公用样式== */
// 全尺寸样式
%full-size {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
}

// 内联
%ib {
  display: inline-block;
  zoom: 1; *display: inline;
  letter-spacing: normal;
  word-spacing: normal;
  vertical-align: middle;
}

// 清除float浮动坍塌
%fix {
  zoom:1;
  &:after {
    display:block;
    content:'.';
    clear:both;
    line-height:0;
    visibility:hidden;
  }

}

// 溢出隐藏
%ellipsis {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

// 像素转em
@function px2em($px, $base-font-size: 54px) {
    @if (unitless($px)) {
        @warn "Assuming #{$px} to be in pixels, attempting to convert it into pixels for you";
        @return px2em($px + 0px); // That may fail.
    } @else if (unit($px) == em) {
        @return $px;
    }
    @return ($px / $base-font-size) * 1em;
}

@function px2rem($px, $base-font-size: 54px) {
    @return (($px / $base-font-size) * 1rem);
}







// 混合宏
@mixin px2rem($property, $px-values, $baseline-px:54px, $support-for-ie:false){
    //Conver the baseline into rems
    $baseline-rem: $baseline-px / 1rem * 1;
    //Print the first line in pixel values
    @if $support-for-ie {
        #{$property}: $px-values;
    }
    //if there is only one (numeric) value, return the property/value line for it.
    @if type-of($px-values) == "number"{
        #{$property}: $px-values / $baseline-rem;
    }
    @else {
        //Create an empty list that we can dump values into
        $rem-values:();
        @each $value in $px-values{
            // If the value is zero or not a number, return it
            @if $value == 0 or type-of($value) != "number"{
                $rem-values: append($rem-values, $value / $baseline-rem);
            }
        }
        // Return the property and its list of converted values
        #{$property}: $rem-values;
    }
}